import { Category } from "../interfaces/Categories.interface";
import ApiService from "./ApiService";

/**
 * Instancia de ApiService con la URL base configurada.
 * @constant {ApiService}
 */
const api = new ApiService("https://api.escuelajs.co/api/v1/");

/**
 * Servicios relacionados con las categoras.
 */
export const categoryServices = {
    /**
     * Obtiene todas las categoras disponibles.
     * @returns {Promise<Category[]>} Lista de categoras.
     */
    getCategories: () => api.getAll<Category>("categories"),

    /**
     * Obtiene una categora por su ID.
     * @param {number} id - Identificador de la categora.
     * @returns {Promise<Category>} Categora encontrada.
     */
    getCategoryById: (id: number) => api.get<Category>(`categories/${id}`),

    /**
     * Crea una nueva categora.
     * @param {Category} data - Datos de la categora a crear.
     * @returns {Promise<Category>} Categora creada.
     */
    createCategory: (data: Category) => api.post<Category>("categories", data),

    /**
     * Actualiza una categora existente.
     * @param {number} id - Identificador de la categora.
     * @param {Category} data - Datos actualizados de la categora.
     * @returns {Promise<Category>} Categora actualizada.
     */
    updateCategory: (id: number, data: Category) => api.put<Category>(`categories/${id}`, data),

    /**
     * Elimina una categora por su ID.
     * @param {number} id - Identificador de la categora.
     * @returns {Promise<void>} Confirmacin de eliminacin.
     */
    deleteCategory: (id: number) => api.delete<void>(`categories/${id}`),

    /**
     * Obtiene todas las categoras de un producto por su ID.
     * @param {number} id - Identificador del producto.
     * @returns {Promise<Category[]>} Lista de categoras del producto.
     */
    getCategoriesByIdProduct: (id: number) => api.get<Category[]>(`categories/${id}/products`),
};
